.profile{
    background: rgb(250, 250, 250);
    border-radius: 6px;
    width: 100%;
    display: inline-block;
}

.profile_name {
    line-height: 18px;
    padding: 20px;
}

.profile_name .name{
    text-transform: uppercase;
    font-size: 17px;
    font-weight: bold;
}

.profile_name .title{
    color:#777;
    font-size: 13px;
    font-weight: bold;
}

.profile_image{
    margin-top: -10px;
    -webkit-clip-path: polygon(0 9%, 100% 0, 100% 94%, 0% 100%);
    clip-path: polygon(0 9%, 100% 0, 100% 94%,0% 100%);
}

.profile_image img{
    width:100%;
}

/* Information */
.profile_information{
    top: 0;
    left: 0;
    margin: -40px 10px 30px;
}

.profile_information .timeline .timeline_content{
    margin-top: 2px;
    margin-bottom: 12 px;
}

.timelineItem_text{
    font-size: 13.5px !important;
    color: #ad6250;
   font-weight: 400 !important; 
}

.timelineItem_text > a{
    color: #6d2bd8;
}

.timelineItem_text > span{
    color: #050505;
 
}

.profile .button_container{
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 10px;

}